import DomainConfiguration from "./DomainConfiguration";
import RoutesConfiguration from "./RoutesConfiguration";
import ResourceConfiguration from "./ResourceConfiguration";

export default {
    name: 'Configuration',
    prefix: "/vue",
    // isApp: true,
    props: {
        isOnline: DomainConfiguration.isOnline,
        isApp: DomainConfiguration.isApp,
    },
    data() {
        return {
            Domain: DomainConfiguration,
            error: false,
            errors: []
        }
    },
    // hostName: "https://www.letztechance.org",
    // host: "https://www.letztechance.org/vue",
    Domain: DomainConfiguration,
    hostName: DomainConfiguration.isOnline !== undefined && DomainConfiguration.isOnline == true ? DomainConfiguration.Domain.domainprod.webservice : DomainConfiguration.Domain.domain.webservice,
    host: DomainConfiguration.isOnline !== undefined && DomainConfiguration.isOnline === true ? DomainConfiguration.Domain.domainprod.url : DomainConfiguration.Domain.domain.url,
    resourceConfiguration: ResourceConfiguration,
    subtitle: ResourceConfiguration.subtitle,
    toptitle: ResourceConfiguration.toptitle,
    bottomtitle: ResourceConfiguration.bottomtitle,


    routes: RoutesConfiguration.routes,
    openlink: "http://openlink.letztechance.org/?",
    external: {
        api: {
            letztechance: {
                openlink: "http://openlink.letztechance.org/?",
                openlinkSec: "https://openlink.letztechance.org/?",

            },
            google: {
                yturl: "https://www.google.com",

            },
            youtube: {
                yturl: "https://www.youtube.com/watch?v=",
                src: "https://www.youtube.com/iframe_api",

            }

        }

    },
    methods: {
        getCircularReplacer() {
            const seen = new WeakSet();
            return (key, value) => {
                if (typeof value === "object" && value !== null) {
                    if (seen.has(value)) {
                        return;
                    }
                    seen.add(value);
                }
                return value;
            };
        },
    }
}