import Vue from 'vue'
import Vuex from 'vuex'
import api from './components/backend-api'

import Cookie from './components/api/cookie/Cookie'

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        loginSuccess: false,
        loginError: false,
        userName: null,
        userPass: null,
        session: null
    },
    mutations: {
        login_success(state, payload) {
            state.loginSuccess = true;
            state.userName = payload.userName;
            state.userPass = payload.userPass;
            state.session = payload.session;
        },
        login_error(state, payload) {
            // console.log(JSON.stringify(state));
            // console.log(JSON.stringify(payload));
            state.loginError = true;
            state.userName = payload.userName;
        }
    },
    actions: {
        login({ commit }, { user, password }) {
            try {
                return new Promise((resolve, reject) => {
                    // console.log("Accessing backend with user: '" + user + " PWD:" + password);
                    console.log("Accessing backend with user: '" + user);

                    api.getSecured(user, password)
                        .then(response => {
                            // console.log("Response: '" + JSON.stringify(response.data) + "' with Statuscode " + response.status);
                            if (response.status == 200) {
                                // console.log("Request successful");
                                var res = response.data.data != undefined ? response.data.data[0] : null;
                                if (res !== undefined && res != null && res.session != "anonymous") {
                                    // console.log("Login successful ");
                                    //console.log("Login successful " + res.session);
                                    Cookie.createCookie("PHPSESSID", res.session, 1, "Strict");
                                    // place the loginSuccess state into our vuex store
                                    commit('login_success', {
                                        userName: user,
                                        userPass: password,
                                        session: res.session
                                    });
                                } else {
                                    // console.log("Not logged in.");
                                    commit('login_error', {
                                        userName: user
                                    });
                                    reject("Invalid credentials!");
                                }
                            }
                            resolve(response)
                        })
                        .catch(error => {
                            console.error("Error: " + error);
                            console.error(error.stack);
                            // place the loginError state into our vuex store
                            commit('login_error', {
                                userName: user
                            });
                            reject("Invalid credentials!");
                        })
                })

            } catch (error) {
                console.error(error);
                console.error(error.stack);
            }
        }
    },
    getters: {
        isLoggedIn: state => state.loginSuccess,
        hasLoginErrored: state => state.loginError,
        getUserName: state => state.userName,
        getUserPass: state => state.userPass,
        getSession: state => state.session !== undefined ? state.session : Cookie.readCookie("PHPSESSID"),
        removeSession: function removeSession() { Cookie.eraseCookie("PHPSESSID"); }
    }
})