<template>
  <div class="index">
    <b-breadcrumb :items="bcitems"></b-breadcrumb>
    <div class="shadowsubject">
      <!-- <p>Index - {{ currentPage }}</p> -->
      <b-tabs content-class="mt-3">
        <b-tab title="Index" active>
          <!-- <b-card header="Foren"> -->

          <div class="overflow-auto">
            <b-pagination
              v-model="currentPage"
              :total-rows="rows"
              :per-page="perPage"
              :current-page="currentPage"
              aria-controls="my-table"
              @change="callIndexApi"
            ></b-pagination>

            <b-list-group>
              <b-list-group-item
                id="my-table"
                :per-page="perPage"
                v-for="item in indexResponse.list"
                :key="item.id"
                v-bind:href="cfg.host + '/list/' + item.id + '/1'"
                class="article"
              >
                {{ item.name }}
              </b-list-group-item>
            </b-list-group>
          </div>
        </b-tab>

        <b-tab title="More" @click="startLoading()">
          <p>More statistics coming soon...</p>
        </b-tab>
      </b-tabs>
    </div>

    <hr />
  </div>
</template>

<script>
import Configuration from "../config/Configuration";
import api from "../backend-api";
import Routes from "../config/Routes";
export default {
  name: "index",
  props: {
    hellomsg: { type: String, required: true },
  },

  data() {
    return {
      perPage: 3,
      currentPage:
        this.$route.params.id !== undefined ? this.$route.params.id : 1,
      cfg: Configuration,
      msg: Routes.routes["index"].desc,
      indexList: "",
      indexResponse: [],
      errors: [],
      context: null,
      progressvalue: 45,
      calvalue: "",
      max: 100,
      loading: false,
      loadingTime: 0,
      maxLoadingTime: 3,
      bcitems: [
        {
          text: "Home",
          href: "/",
        },
        {
          text: "Index",
          href: "/index/1",
        },
        {
          text: "" + this.currentPage !== undefined ? this.currentPage : "1",
          href: "/list/1",
          active: true,
        },
      ],
    };
  },
  computed: {
    rows() {
      return this.indexResponse.size;
    },
  },
  watch: {
    loading(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.clearLoadingTimeInterval();

        if (newValue) {
          this.$_loadingTimeInterval = setInterval(() => {
            this.loadingTime++;
          }, 1000);
        }
      }
    },
    loadingTime(newValue, oldValue) {
      if (newValue !== oldValue) {
        if (newValue === this.maxLoadingTime) {
          this.loading = false;
        }
      }
    },
  },

  methods: {
    onContext(ctx) {
      this.context = ctx;
    },
    // Fetches posts when the component is created.
    callIndexApi(id) {
      console.log("read:" + id);
      //this.currentPage = id;
      api
        .getIndex(id)
        .then((response) => {
          this.indexResponse = response.data;

          console.log(response.data);
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
    setProgressValue() {
      this.progressvalue = Math.random() * this.max;
    },
    clearLoadingTimeInterval() {
      clearInterval(this.$_loadingTimeInterval);
      this.$_loadingTimeInterval = null;
    },
    startLoading() {
      this.loading = true;
      this.loadingTime = 0;
    },
  },
  created() {
    this.$_loadingTimeInterval = null;
    this.id = this.$route.params.id !== undefined ? this.$route.params.id : 1;
    console.log("Component has been created!" + this.id);
    console.log(this.id);
    this.callIndexApi(1);
  },

  mounted() {
    this.startLoading();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
.article {
  text-align: left;
}
.shadowsubject {
  margin: 10px;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
}
</style>
