<script>
import Configuration from "./components/config/Configuration";
import api from "./components/backend-api";
// import { Slide } from "vue-burger-menu";
import Routes from "./components/config/Routes";
import Util from "./components/api/util";

export default {
  name: "app",
  // util: Util,
  components: {
    // Slide, // Register your component
  },
  data() {
    return {
      msg: Routes.routes["home"].desc,
      indexResponse: [],
      inputSearch: "",
      searchout: "",
      cfg: Configuration,
      util: Util
    };
  },
  methods: {
    getSearch(s) {
      s.preventDefault();
      this.searchout = "LETZTECHANCE.ORG - Search:" + this.inputSearch;
      var url = this.cfg.host + "/search/" + encodeURI(this.inputSearch);
      // alert("URL:" + url);
      window.location = url;
    },
    // Fetches posts when the component is created.
    callgetFullIndex() {
      api
        .getFullIndex()
        .then((response) => {
          this.indexResponse = response.data;
          // console.log(response.data);
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
    reduceText(s) {
      return s.length > 30 ? s.substr(0, 20) + "..." : s;
    },
  },
  created() {
    // console.log("App running...");
    this.callgetFullIndex();
  },
};
</script>

