<template>
  <div class="welcome">
    <div class="shadowsubject">
      <b-breadcrumb :items="items"></b-breadcrumb>
      <!-- <img src="./../assets/logomain.png" /> -->
      <runtime-template-compiler
        :template="cfg.resourceConfiguration.images.logo"
      />

      <h1>{{ $t("message.welcome") }}</h1>

      <h3>{{ util.capitalizeFirstLetter( $t("message.search")) }} {{ $t("message.and") }} {{ $t("message.find") }}</h3>

      <div class="d-flex align-items-center mb-3">
        <b-progress class="w-100" :max="maxLoadingTime" height="1.5rem">
          <b-progress-bar
            :value="
              ((loadingTime / maxLoadingTime) * 100).toFixed(2) * (1.5 / 10)
            "
            variant="danger"
          ></b-progress-bar>

          <b-progress-bar
            :value="
              ((loadingTime / maxLoadingTime) * 100).toFixed(2) * (2.5 / 10)
            "
            variant="warning"
          ></b-progress-bar>
          <b-progress-bar
            :value="
              ((loadingTime / maxLoadingTime) * 100).toFixed(2) * (6 / 10)
            "
            variant="success"
          ></b-progress-bar>
        </b-progress>
        <b-progress-bar
          :value="loadingTime"
          :label="`${((loadingTime / maxLoadingTime) * 100).toFixed(2)}%`"
        ></b-progress-bar>

        <b-button class="ml-3" @click="startLoading()">{{ util.capitalizeFirstLetter( $t("message.reload")) }}</b-button>
      </div>

      <div v-if="cfg.isApp">
        <b-badge variant="success">Application</b-badge>
        {{ cfg.isApp }} <b-badge variant="success">successful</b-badge>
      </div>
    </div>
    <!-- <div v-if="errors">
      <b-badge variant="warning">API call</b-badge> {{ errors }} <b-badge variant="warning">NOT successful</b-badge>
    </div> -->
    <!-- Rows:{{ rows / perPage }} -->
    <div class="shadowsubject">
      <b-pagination
        v-model="currentPage"
        :total-rows="rows"
        :per-page="perPage"
        :current-page="currentPage"
        aria-controls="my-table"
        @change="callIndexApi"
      ></b-pagination>

      <b-tabs content-class="mt-3">
        <b-tab v-if="cfg.isApp" title="App" @click="startLoading()">
          <p>.</p>
        </b-tab>

        <b-tab title="Foren" active>
          <!-- <p>All {{ currentPage }}</p> -->

          <div class="overflow-auto">
            <!-- <b-tab title="Article"> -->
            <b-tabs content-class="mt-3">
              <b-tab
                id="my-tabs"
                :per-page="perPage"
                v-for="item in indexResponse.list"
                :title="item.name"
                :key="item.id"
                @click="callListApi(item.id)"
              >
                <div class="shadowborder">
                  <h1>{{ item.name }}</h1>
                </div>
                <div class="shadowborder overflow-auto">
                  <b-list-group>
                    <b-list-group-item
                      id="list-table"
                      :per-page="perPage"
                      :current-page="currentPage"
                      v-for="item in listResponse.row"
                      :key="item.id"
                      v-bind:href="
                        cfg.host + '/read/' + item.page + '/' + item.id
                      "
                      class="article"
                    >
                      {{ item.subject }}
                    </b-list-group-item>
                  </b-list-group>
                </div>
              </b-tab>
            </b-tabs>
          </div>
        </b-tab>

        <b-tab title="All">
          <!-- <p>All {{ currentPage }}</p> -->

          <div class="overflow-auto">
            <b-list-group>
              <b-list-group-item
                id="my-table"
                :per-page="perPage"
                :current-page="currentPage"
                v-for="item in indexResponse.list"
                :key="item.id"
                v-bind:href="cfg.host + '/list/' + item.id + '/1'"
                class="article"
              >
                {{ item.name }}
              </b-list-group-item>
            </b-list-group>
          </div>

          <!-- <h4>{{ indexResponse }}</h4> -->
        </b-tab>

        <b-tab title="More" @click="startLoading()">
          <ul id="homeindex">
            <li
              v-for="item in indexResponse.list"
              :key="item.name"
              v-bind:href="'list-' + item.id + '-1.html'"
            >
              <a v-bind:href="'list-' + item.id + '-1.html'">
                {{ item.name }}
              </a>
            </li>
          </ul>          
          <p>.</p>
        </b-tab>
      </b-tabs>
    </div>

    <hr />
  </div>
</template>

<script>
import Vue from "vue";
import api from "../backend-api";
import Routes from "../config/Routes";
import Configuration from "../config/Configuration";
import Util from "../api/util";
export default {
  name: "welcome",
  props: {
    hellomsg: { type: String, required: true },
  },
  computed: {
    rows() {
      return this.indexResponse["size"] !== undefined
        ? +this.indexResponse["size"] - 2
        : 0;
    },
  },
  data() {
    return {
      vue: Vue,
      forumSize: 92,
      perPage: 15,
      currentPage: 1,
      cfg: Configuration,
      util: Util,
      source: Configuration.bottomtitle,
      msg: Routes.routes["home"].desc,
      indexResponse: [],
      listResponse: [],      
      errors: [],
      context: null,
      progressvalue: 45,
      calvalue: "",
      max: 100,
      loading: false,
      loadingTime: 0,
      maxLoadingTime: 3,
      message: "default from the template compiler",
      template:
        `
          <h1>{{ message }}</h1>
        ` + Configuration.bottomtitle,

      items: [
        {
          text: "Home",
          href: "/vue",
        },
        {
          text: "Index",
          href: "/vue/index.html",
          active: true,
        },
      ],
    };
  },
  watch: {
    loading(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.clearLoadingTimeInterval();

        if (newValue) {
          this.$_loadingTimeInterval = setInterval(() => {
            this.loadingTime++;
          }, 1000);
        }
      }
    },
    loadingTime(newValue, oldValue) {
      if (newValue !== oldValue) {
        if (newValue === this.maxLoadingTime) {
          this.loading = false;
        }
      }
    },
  },

  methods: {
    // computed: {
    //   dynamicComponent() {
    //     return {
    //       template: this.source,
    //     };
    //   },
    // },
    onContext(ctx) {
      this.context = ctx;
    },
    htmlCompile(s) {
      // return Vue.compile(s);
      return s;
    },
    // Fetches posts when the component is created.
    callIndexApi(value) {
      this.currentPage = value;
      this.currentPage = this.currentPage !== undefined ? this.currentPage : 1;
      api
        .getIndex(this.currentPage)
        .then((response) => {
          this.indexResponse = response.data;
          this.forumSize = this.indexResponse.size - 1;
          // console.log('OUT:'+JSON.stringify(this.indexResponse));
          // console.log('V:'+this.indexResponse["size"]);
          this.indexList = this.callRenderIndex(this.indexResponse);
          // console.log(response.data);
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
    callListApi(value) {
      this.currentPage = value !== undefined ? value : 1;
      this.id = this.$route.params !== undefined ? this.$route.params.id : 1;
      this.id = this.id !== undefined ? this.id : 1;
      this.getListApi(value, 1);
    },
    getListApi(id, pageId) {
      id = id !== undefined ? id : 1;
      pageId = pageId !== undefined ? pageId : 1;
      console.log("ID:" + id + " Page:" + pageId);
      api
        .getList(id, pageId)
        .then((response) => {
          this.listResponse = response.data;
          console.log(response.data);
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
    setProgressValue() {
      this.progressvalue = Math.random() * this.max;
    },
    callRenderIndex(data) {
      var result = "";
      result += "<ul>";
      for (v in data) {
        result += "<li>" + data[v] + "</li>";
      }
      result += "</ul>";
      return result;
    },
    clearLoadingTimeInterval() {
      clearInterval(this.$_loadingTimeInterval);
      this.$_loadingTimeInterval = null;
    },
    startLoading() {
      this.loading = true;
      this.loadingTime = 0;
    },
  },
  created() {
    this.$_loadingTimeInterval = null;
    // console.log("Component has been created!");
    this.callIndexApi();
    this.callListApi(1, 1);
  },

  mounted() {
    this.startLoading();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #864c00;
}
.article {
  text-align: left;
}
.shadowsubject {
  margin: 10px;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
}
</style>
