<template>
  <div class="service">
    <h1>{{ msg }}</h1>
    <div class="unprotected" v-if="loginError">
      <h1>
        <b-badge variant="danger"
          >You don't have rights here, please try again :D</b-badge
        >
      </h1>
      <h5>Seams that you don't have access rights...</h5>
      <a href="./login">Back</a>
    </div>
    <div class="unprotected" v-else>
      <div class="unprotected" v-if="getAuth">
        <h1>Logged in</h1>
        <button @click="callIsAuthApi()">Call rest authorization backend service</button>
      </div>
      <div class="unprotected" v-else>
        <h1>Access denied</h1>
        <h1><b-badge variant="info">Please login</b-badge></h1>
        <h5>You're not logged in. Please try again?</h5>

        <form @submit.prevent="callLogin()">
          <input type="text" placeholder="username" v-model="user" />
          <input type="password" placeholder="userpass" v-model="userpass" />
          <b-btn variant="success" type="submit">Login</b-btn>
          <p v-if="error" class="error">Bad login information</p>
        </form>
      </div>
    </div>
    <hr />

    

    <hr />
    <ul id="homeindex">
      <li v-for="item in backendResponse.list" :key="item.name">
        {{ item.name }}
      </li>
    </ul>
    <hr />

    <h4>Backend response: {{ backendResponse }}</h4>
  </div>
</template>

<script>
import api from "../backend-api";
//import Routes from './components/Routes';
import store from "../../store";
export default {
  name: "service",

  data() {
    return {
      loginError: false,
      user: "",
      userpass: "",
      session: "",
      error: false,
      errors: [],
      msg: "Services:",
      backendResponse: [],
      errors: [],
    };
  },
  methods: {
    //todo:check session again !!!
    getAuth() {
      return this.store.getters.getSession !== undefined &&
        this.store.getters.getSession !== "anonymous"
        ? true
        : false;
    },
    callIsAuthApi() {
      api
        .getIndex(1)
        .then((response) => {
          this.backendResponse = response.data;
          console.log(response.data);
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
  },
  created() {
    console.log("Component has been created!");
    this.callIsAuthApi();
  },
};
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>
