<template>
  <div class="list shadowsubject2">
    <b-breadcrumb :items="bcitems"></b-breadcrumb>

    <div class="shadowsubject">
      <b-tabs content-class="mt-3">
        <b-tab :title="listResponse.tableName" active>
          <!-- <p>List {{ $route.params.id }} - {{ $route.params.pageid }}</p> -->
          <!-- <div>Forum: {{ $route.params.id }} - {{ $route.params.pageid }}</div> -->
          <!-- <b-card header="Foren"> -->

          <div class="overflow-auto">
            <b-pagination
              v-model="currentPage"
              :total-rows="rows"
              :per-page="perPage"
              :currentPage="currentPage"
              :current-page="$route.params.pageid"
              aria-controls="list-table"
              @change="callListApi"
            ></b-pagination>

            <b-list-group class="listul">
              <b-list-group-item
                id="list-table"
                :per-page="perPage"
                :current-page="currentPage"
                v-for="item in listResponse.row"
                :key="item.id"
                v-bind:href="cfg.host + '/read/' + item.page + '/' + item.id"
                class="article"
              >
                {{ item.subject }}
              </b-list-group-item>
            </b-list-group>
          </div>
          <!-- </b-card> -->

          <!-- <h4>{{ listResponse }}</h4> -->
        </b-tab>
        <!-- <b-tab title="More" @click="startLoading()">
          <p>More</p>
          <b-row>
            <b-col md="auto">
              <b-calendar
                v-model="calvalue"
                @context="onContext"
                locale="en-US"
              ></b-calendar>
            </b-col>
            <b-col>
              <p>
                Value: <b>'{{ calvalue }}'</b>
              </p>
              <p class="mb-0">Context:</p>
              <pre class="small">{{ context }}</pre>
            </b-col>
          </b-row>
        </b-tab> -->
      </b-tabs>
    </div>
  </div>
</template>

<script>
import api from "../backend-api";
import Routes from "../config/Routes";
import Configuration from "../config/Configuration";
export default {
  name: "list",
  id: 0,
  props: {
    hellomsg: { type: String, required: true },
  },
  computed: {
    rows() {
      return this.listResponse.size !== undefined ? this.listResponse.size : 0;
    },
  },
  data() {
    return {
      perPage: 3,
      currentPage: 1,
      cfg: Configuration,
      msg: Routes.routes["list"].desc,
      searchResponse: [],
      listResponse: [],
      errors: [],
      context: null,
      progressvalue: 45,
      calvalue: "",
      max: 100,
      loading: false,
      loadingTime: 0,
      maxLoadingTime: 3,
      bcitems: [],
    };
  },
  watch: {
    loading(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.clearLoadingTimeInterval();

        if (newValue) {
          this.$_loadingTimeInterval = setInterval(() => {
            this.loadingTime++;
          }, 1000);
        }
      }
    },
    loadingTime(newValue, oldValue) {
      if (newValue !== oldValue) {
        if (newValue === this.maxLoadingTime) {
          this.loading = false;
        }
      }
    },
  },

  methods: {
    onContext(ctx) {
      this.context = ctx;
    },
    getBreadCrumb() {
      console.log("rebuild breadcrumb");
      return [
        {
          text: "Home",
          href: "/",
        },
        {
          text: "Index",
          href: "/index",
        },
        {
          text:
            "" + this.listResponse !== undefined
              ? this.listResponse.tableName
              : "",
          href:
            "/list/" + this.listResponse !== undefined
              ? this.listResponse.table
              : "1",
        },
        {
          text:
            "" + this.listResponse !== undefined
              ? this.listResponse.table
              : "N/A",
          active: true,
        },
      ];
    },
    // Fetches posts when the component is created.
    callListApi(value) {
      this.currentPage = value !== undefined ? value : 1;
      this.id = this.$route.params !== undefined ? this.$route.params.id : 1;
      this.id = this.id !== undefined ? this.id : 1;
      this.getListApi(this.id, this.currentPage);
    },
    getListApi(id, pageId) {
      id = id !== undefined ? id : 1;
      pageId = pageId !== undefined ? pageId : 1;
      // console.log("ID:" + id+" Page:"+pageId);
      api
        .getList(id, pageId)
        .then((response) => {
          this.listResponse = response.data;
          this.bcitems = this.getBreadCrumb();
          // console.log(response.data);
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
    setProgressValue() {
      this.progressvalue = Math.random() * this.max;
    },

    clearLoadingTimeInterval() {
      clearInterval(this.$_loadingTimeInterval);
      this.$_loadingTimeInterval = null;
    },
    startLoading() {
      this.loading = true;
      this.loadingTime = 0;
    },
  },
  created() {
    this.$_loadingTimeInterval = null;
    console.log("Component has been created!");
    this.callListApi();
  },

  mounted() {
    this.startLoading();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #864c00;
}
.article {
  text-align: left;
}
.shadowsubject {
  margin: 10px;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
}
</style>
