<template>
  <div class="imprint">
    <b-breadcrumb :items="bcitems"></b-breadcrumb>

    <div class="shadowsubject">
      <b-tabs content-class="mt-3">
        <b-tab :title="readResponse.subject+''" active>
          <!-- <p>News - Page {{ currentPage }} - {{ $route.params.id }} - {{ $route.params.pageid }}</p> -->

          <!-- <div class="overflow-auto">
            <b-pagination
              v-model="currentPage"
              :total-rows="rows"
              :per-page="perPage"
              :currentPage="currentPage"
              :current-page="$route.params.pageid"
              aria-controls="my-table"
              @change="callReadApi"
            ></b-pagination>
          </div> -->
          <h1 class="readsubject">{{ readResponse.subject }}</h1>
          <hr />
          <p v-html="readResponse.body" class="readbody"></p>

          <!-- <h4>{{ indexResponse }}</h4> -->
        </b-tab>
       
      </b-tabs>
    </div>
  </div>
</template>

<script>
import api from "../backend-api";
import Routes from "../config/Routes";
import Configuration from "../config/Configuration";
export default {
  name: "imprint",
  props: {
    hellomsg: { type: String, required: true },
  },

  data() {
    return {
      id: this.$route.params.id !== undefined ? this.$route.params.id : 1,
      pageid:
        this.$route.params.pageid !== undefined ? this.$route.params.pageid : 1,
      tableName:'',
      cfg: Configuration,
      perPage: 1,
      currentPage: this.pageid !== undefined ? this.pageid : 1,
      msg: Routes.routes["imprint"].desc,
      readResponse: [],
      errors: [],
      context: null,
      progressvalue: 45,
      calvalue: "",
      max: 100,
      loading: false,
      loadingTime: 0,
      maxLoadingTime: 3,
      bcitems: [
        {
          text: "Home",
          href: Configuration.host + "/",
        },
        {
          text: "Index",
          href: Configuration.host + "/index",
        },
        {
          text: "Imprint",
          href: Configuration.host + "/imprint",
          active: true,
        }        
      ],
    };
  },
  computed: {
    rows() {
      return this.readResponse.size;
    },
  },
  watch: {
    loading(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.clearLoadingTimeInterval();

        if (newValue) {
          this.$_loadingTimeInterval = setInterval(() => {
            this.loadingTime++;
          }, 1000);
        }
      }
    },
    loadingTime(newValue, oldValue) {
      if (newValue !== oldValue) {
        if (newValue === this.maxLoadingTime) {
          this.loading = false;
        }
      }
    },
  },

  methods: {
    onContext(ctx) {
      this.context = ctx;
    },
    callReadApi(value) {
      this.currentPage = value;
      this.getReadApi(this.id, value);
    },
    getReadApi(id, pageId) {
      this.currentPage = pageId;
      api
        .getRead(id, pageId)
        .then((response) => {
          this.readResponse = response.data;
          this.tableName = this.readResponse.tableName;
          console.log(response.data);
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
    setProgressValue() {
      this.progressvalue = Math.random() * this.max;
    },

    clearLoadingTimeInterval() {
      clearInterval(this.$_loadingTimeInterval);
      this.$_loadingTimeInterval = null;
    },
    startLoading() {
      this.loading = true;
      this.loadingTime = 0;
    },
  },
  created() {
    this.getReadApi(76, 1);
  },

  mounted() {
    this.startLoading();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #864c00 !important;
}
.readsubject {
  border: 2px solid rgb(178, 83, 0);
  border-radius: 15px;
  margin: 10px;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
}
.readbody {
  border: 2px solid rgb(178, 83, 0);
  border-radius: 15px;
  text-align: left;
  margin: 10px;
  padding: 2px;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
  min-height: 400px;
}
</style>
