<script>
import Configuration from "../../../config/Configuration";

export default {
  name: "CoreAPI",
  id: 0,
  player: null,
  props: {
    hellomsg: { type: String, required: true },
  },
  data() {
    return {
      done: false,
    };
  },
  methods: {
    getLinks(text) {
      var list = [];
      var regex = /href\s*=\s*(['"])(https?:\/\/.+?)\1/gi;
      var link;
      while ((link = regex.exec(text)) !== null) {
        var l = link[2]
          .toString()
          .replace(Configuration.external.api.letztechance.openlinkSec, "");
        list.push(link[2]);
      }
      return list;
    },

    getLinksValues(links) {
      var list = [];
      try {
        for (var v in links) {
          var link = links[v];
          // console.log("v:" + link);
          if (
            link !== undefined &&
            link.toString().trim().startsWith("http") &&
            link.toString().includes("http")
          ) {
            link = link
              .replace(Configuration.external.api.letztechance.openlinkSec, "")
              .replace(Configuration.external.api.letztechance.openlink, "");
            // .replace(Configuration.external.api.youtube.yturl, "");
            console.log("found:" + link);
            list.push(link);
          } else {
            //TODO:scan for more maybe local scripts value list.push(link);
          }
        }
      } catch (error) {
        console.error(error);
        console.error(error.stack);
      }
      return list;
    },
    getYoutubeVideoLinks(links) {
      var anchors = [];
      for (var s in links) {
        let t = links[s];
        if (t !== undefined && t.length > 0 && t.toString().includes(Configuration.external.api.youtube.yturl)) {
          if (t.toString().replace('"').startsWith("http")) {
            var url = t.replace(Configuration.external.api.youtube.yturl, "");
            anchors.push(url);
          }
        }
      }
      return anchors;
      // return this.getHrefs(links, Configuration.external.api.youtube.yturl);
    },
    getFinalBody(links) {
      var text = "<h1>404 - Not found</h1>";
      var api = Configuration.external.api.youtube.yturl;
      try {
        // console.log(api);
        for (var v in links) {
          // console.log("LINK:" + links[v]);
          var link = links[v];
          if (link !== undefined) {
            var id = link.replace(api, "");
            text = "" + this.getYoutubeVideo(id);
          }
        }
      } catch (error) {
        console.error(error);
      }
      return "" + text;
    },
    getYoutubeVideo(text) {
      // console.log("t:" + text);
      return '<a href="' + text + '">' + text + "</a>";
    },
    getYoutubeVideos(anchors) {
      try {
        var i,
          len = anchors.length;
        // console.log(JSON.stringify(anchors));
        // console.log("TEST");
        for (i = 0; i < l7en; i++) {
          // console.log(JSON.stringify(anchors[i]));
          var tag = document.createElement("script"); //var vId = "M7lc1UVf-VE";
          tag.src = Configuration.external.api.youtube.src;
          var firstScriptTag = document.getElementsByTagName("script")[0];
          // console.log(JSON.stringify(firstScriptTag));
          firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
          this.onYouTubeIframeAPIReady();
        }
      } catch (error) {
        console.error(error);
      }
      return "" + text;
    },
    getHrefs(links, api) {
      var anchors = [];
      for (var s in links) {
        let t = links[s];
        if (t !== undefined && t.length > 0 && t.toString().includes(api)) {
          if (t.toString().replace('"').startsWith("http")) {
            var url = t.replace(Configuration.openlink + api, "");
            anchors.push(url);
          }
        }
      }
      return anchors;
    },
    getAnchors() {
      return document.getElementsByTagName("a");
    },
    printAnchors(text, api) {
      var anchors = this.getAnchors();
      var i,
        len = anchors.length;

      for (i = 0; i < len; i++) {
        console.log(anchors[i].getAttribute("href"));
      }
    },
    onYouTubeIframeAPIReady() {
      console.log("running player...");
      this.player = new YT.Player("player", {
        height: "300",
        width: "480",
        videoId: vId,
        events: {
          onReady: this.onPlayerReady,
          onStateChange: this.onPlayerStateChange,
        },
      });
    },

    // 4. The API will call this function when the video player is ready.
    onPlayerReady(event) {
      event.target.playVideo();
    },

    // 5. The API calls this function when the player's state changes.
    //    The function indicates that when playing a video (state=1),
    //    the player should play for six seconds and then stop.

    onPlayerStateChange(event) {
      if (event.data == YT.PlayerState.PLAYING && !done) {
        setTimeout(stopVideo, 6000);
        this.done = true;
      }
    },
    stopVideo() {
      this.player.stopVideo();
    },
  },
  updateLinksInHTML(html) {
    var regex = /href\s*=\s*(['"])(https?:\/\/.+?)\1/gi;
    var link;

    while ((link = regex.exec(html)) !== null) {
      html = html.replace(
        link[2],
        Configuration.external.api.letztechance.openlinkSec +
          encodeURIComponent(link[2])
      );
    }
    return html;
  },

  createTextLinks_(text) {
    return (text || "").replace(
      /([^\S]|^)(((https?\:\/\/)|(www\.))(\S+))/gi,
      function (match, space, url) {
        var hyperlink = url;
        if (!hyperlink.match("^https?://")) {
          hyperlink = "http://" + hyperlink;
        }
        return space + '<a href="' + hyperlink + '">' + url + "</a>";
      }
    );
  },
};

