<template>
  
  <div class="unprotected" v-if="loginError">
    <h1>
      <b-badge variant="danger"
        >You don't have rights here, please try again :D</b-badge
      >
    </h1>
    <h5>Seams that you don't have access rights...</h5>
    <a href="./login">Back</a>
  </div>
  <div class="unprotected" v-else>
    <h1><b-badge variant="info"> {{name}}</b-badge></h1>
    <h2><b-badge variant="info">Please login</b-badge></h2>
    <h5>You're not logged in. Please try again?</h5>
    
    <form @submit.prevent="callLogin()">
      <input type="text" placeholder="username" v-model="user" />
      <input type="password" placeholder="userpass" v-model="userpass" />
      <b-btn variant="success" type="submit">Login</b-btn>
      <p v-if="error" class="error">Bad login information</p>
    </form>
  </div>
</template>
<script>
// import Cookie from './cookie/Cookie'
import store from "../../store";
export default {
  name: "logout",
  
  data() {
    return {
      name: ("logout").toLocaleUpperCase(),
      loginError: false,
      user: "",
      userpass: "",
      session: "",
      error: false,
      errors: [],
      store:store
    };
  },
  methods: {
    callLogin() {
      this.errors = [];
      this.$store
        .dispatch("login", { user: this.user, password: this.userpass })
        .then(() => {
          // this.session = Cookie.readCookie("PHPSESSID");
          // console.log("Session:"+this.session);
          // console.log("Session:"+this.$store.getters.getSession);
          this.$router.push("/Protected");
        })
        .catch((error) => {
          this.loginError = true;
          this.errors.push(error);
          this.error = true;
        });
    },
  },
  created() {
    console.log("Session:" + this.$store.getters.getSession);
    //Cookie.readCookie("PHPSESSID");
    // this.$store.getters.removeSession();
    this.store.getters.removeSession;
    // if (
    //   this.$store.getters.getSession !== undefined &&
    //   this.$store.getters.getSession != "anonymous"
    // )
    //   this.$router.push("/Protected");
  },
};
</script>