import axios from 'axios'

const AXIOS = axios.create({
    baseURL: '/api',
    timeout: 30000
});
import Configuration from "./config/Configuration";
export default {
    hostName: Configuration.hostName,
    welcome() {
        return AXIOS.get(this.hostName + '/webservices/client.php?q=getFullIndexJSON');
    },
    getIndex(id) {
        return AXIOS.post(this.hostName + '/webservices/client.php?q=getIndexJSON&value1=' + id, {
            q: 'getReadJSON',
            value1: id

        })
    },
    getFullIndex() {
        return AXIOS.get(this.hostName + '/webservices/client.php?q=getFullIndexJSON');
    },
    getList(id, pageId) {
        return AXIOS.post(this.hostName + '/webservices/client.php?q=getListJSON&value1=' + id + '&value2=' + pageId, {
            q: 'getReadJSON',
            value1: id,
            value2: pageId

        })
    },
    getRead(id, articleId) {
        return AXIOS.get(this.hostName + '/webservices/client.php?q=getReadJSON&value1=' + id + '&value2=' + articleId, {
            value1: id,
            value2: articleId
        })
    },
    //https://www.letztechance.org/webservices/client.php?q=getSearch&value1=79&value2=1&query=alien
    getSearch(id, pageId, query) {
        return AXIOS.post(this.hostName + '/webservices/client.php?q=getSearch&value1=' + id + '&value2=' + pageId + '&query=' + query, {
            q: 'getSearch',
            value1: id,
            value2: pageId

        })
    },
    //
    getUser(userId) {
        return AXIOS.get(this.hostName + '/webservice/client.php?q=/user/' + userId);
    },
    createUser(firstName, lastName) {
        return AXIOS.post(this.hostName + '/hc/webservice/client.php?q=/user/' + firstName + '/' + lastName);
    },
    getSecured(user, password) {
        var axiosConfig = {
            headers: {
                'Content-Type': 'application/json;charset=UTF-8',
                'X-Requested-With': 'XMLHttpRequest',
                "Access-Control-Allow-Origin": "*",
                // 'Authorization': 'Basic ' + session,
                // Cookie: "PHPSESSID=" + session + ";",
                withCredentials: true
            }
        };
        var data = {
            withCredentials: true,
            auth: {
                user: user,
                pass: password
            },
            data: {
                user: user,
                pass: password
            },
            user: user,
            pass: password
        };
        var url = this.hostName + '/webservices/client.php?q=getAuth'; // + '&user=' + user + '&pass=' + password;
        // console.log(url);
        return AXIOS.post(url, data, axiosConfig);
    },
    getPost(value1, value2, cdate, subjecttext, bodytext, isProtected, isPHPCode, session) {
        //PHPSESSID
        var axiosConfig = {
            headers: {
                'Content-Type': 'application/json;charset=UTF-8',
                'X-Requested-With': 'XMLHttpRequest',
                //'Content-type': 'application/x-www-form-urlencoded',
                "Access-Control-Allow-Origin": "*",

                //'Authorization': 'Basic ${session}'
                'Authorization': 'Basic ' + session,
                Cookie: "PHPSESSID=" + session + ";",
                withCredentials: true
            }
        };
        var url = this.hostName + '/webservices/client.php?q=doInsert';
        var data = {
            withCredentials: true,
            token: session,
            cdate: cdate,
            value1: value1,
            subject: '' + subjecttext,
            body: '' + bodytext,
            isProtected: isProtected,
            isPHPCode: isPHPCode
        };
        console.log("API:" + JSON.stringify(data));
        return AXIOS.post(url, data, axiosConfig);
        // return AXIOS.get(url, data);
    },
    //deprecated
    testcreated() {
        // POST request using axios with error handling
        const article = { title: "Vue POST Request Example" };
        axios.post("https://reqres.in/invalid-url", article)
            .then(response => this.articleId = response.data.id)
            .catch(error => {
                this.errorMessage = error.message;
                console.error("There was an error!", error);
            });
    },

    authme() {
        // POST request using axios with set headers
        const article = { title: "Vue POST Request Example" };
        const headers = {
            "Authorization": "Bearer my-token",
            "My-Custom-Header": "foobar"
        };
        axios.post("https://reqres.in/api/articles", article, { headers })
            .then(response => this.articleId = response.data.id);
    }
}