<template>
  <div class="Contact">
    <b-breadcrumb :items="bcitems"></b-breadcrumb>

    <div class="shadowsubject">
      <b-tabs content-class="mt-3">
        <b-tab title="Contact" active>
          <!-- <p>News - Page {{ currentPage }} - {{ $route.params.id }} - {{ $route.params.pageid }}</p> -->
          <hr />

          <form>
            <h2 class="x-hidden-focus">E-Mail adress:</h2>
            <div
              class="
                ui-input-text ui-body-inherit ui-corner-all ui-shadow-inset
              "
            >
              <input
                name="from"
                size="40"
                value="peter.mustermann@t-online.de"
                type="text"
              />
            </div>
            <h2>E-Mail Antwort an:</h2>
            <div
              class="
                ui-input-text ui-body-inherit ui-corner-all ui-shadow-inset
              "
            >
              <input
                id="replyto"
                name="replyto"
                size="40"
                value="reply_to@reply_to.net"
                type="text"
              />
            </div>
            <input
              id="recipients"
              name="recipients"
              value="webmaster@letztechance.org"
              type="hidden"
            />
            <input
              id="ccaddress"
              name="ccaddress"
              value="webmaster@letztechance.org"
              type="hidden"
            />
            <h2>Subjekt:</h2>
            <div
              class="
                ui-input-text ui-body-inherit ui-corner-all ui-shadow-inset
              "
            >
              <input id="subject" name="subject" size="30" type="text" />
            </div>
            <h2>Nachricht:</h2>
            <textarea
              rows="6"
              id="message"
              name="message"
              cols="30"
              class="
                ui-input-text
                ui-shadow-inset
                ui-body-inherit
                ui-corner-all
                ui-textinput-autogrow
              "
              style="height: 52px"
            ></textarea>
            <br />
            <h2>Key:</h2>
            <div
              class="
                ui-input-text ui-body-inherit ui-corner-all ui-shadow-inset
              "
            >
              <input name="key" type="text" />
            </div>
            <br /><img v-bind:src="cfg.host+'/captcha.html'" alt="CAPTCHA" /><br />
            <div class="ui-btn ui-input-btn ui-corner-all ui-shadow">
              Versenden<input
                value="Versenden"
                id="btncontactmodal"
                type="button"
              />
            </div>
            oder
            <div class="ui-btn ui-input-btn ui-corner-all ui-shadow">
              Reset<input value="Reset" type="reset" />
            </div>
          </form>
          <hr />
          <!-- <h1 class="readsubject">{{ readResponse.subject }}</h1>
          <hr />
          <p v-html="readResponse.body" class="readbody"></p> -->

          <!-- <h4>{{ indexResponse }}</h4> -->
        </b-tab>
        <b-tab title="More" @click="startLoading()">
          <p>More</p>
          <b-row>
            <b-col md="auto">
              <b-calendar
                v-model="calvalue"
                @context="onContext"
                locale="en-US"
              ></b-calendar>
            </b-col>
            <b-col>
              <p>
                Value: <b>'{{ calvalue }}'</b>
              </p>
              <p class="mb-0">Context:</p>
              <pre class="small">{{ context }}</pre>
            </b-col>
          </b-row>
        </b-tab>
      </b-tabs>
    </div>
  </div>
</template>

<script>
import api from "../backend-api";
import Routes from "../config/Routes";
import Configuration from "../config/Configuration";
export default {
  name: "Contact",
  props: {
    hellomsg: { type: String, required: true },
  },

  data() {
    return {
      id: this.$route.params.id !== undefined ? this.$route.params.id : 1,
      pageid:
        this.$route.params.pageid !== undefined ? this.$route.params.pageid : 1,
      cfg: Configuration,
      perPage: 1,
      currentPage: this.pageid !== undefined ? this.pageid : 1,
      msg: Routes.routes["contact"].desc,
      readResponse: [],
      errors: [],
      context: null,
      progressvalue: 45,
      calvalue: "",
      max: 100,
      loading: false,
      loadingTime: 0,
      maxLoadingTime: 3,
      bcitems: [
        {
          text: "Home",
          href: Configuration.host + "/",
        },
        {
          text: "Index",
          href: Configuration.host + "/index",
        },
        {
          text: "Contact",
          href: Configuration.host + "/contact",
          active: true,
        },
      ],
    };
  },
  computed: {
    rows() {
      return this.readResponse.size;
    },
  },
  watch: {
    loading(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.clearLoadingTimeInterval();

        if (newValue) {
          this.$_loadingTimeInterval = setInterval(() => {
            this.loadingTime++;
          }, 1000);
        }
      }
    },
    loadingTime(newValue, oldValue) {
      if (newValue !== oldValue) {
        if (newValue === this.maxLoadingTime) {
          this.loading = false;
        }
      }
    },
  },

  methods: {
    onContext(ctx) {
      this.context = ctx;
    },
    callReadApi(value) {
      this.currentPage = value;
      this.getReadApi(this.id, value);
    },
    getReadApi(id, pageId) {
      this.currentPage = pageId;
      api
        .getRead(id, pageId)
        .then((response) => {
          this.readResponse = response.data;
          console.log(response.data);
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
    setProgressValue() {
      this.progressvalue = Math.random() * this.max;
    },

    clearLoadingTimeInterval() {
      clearInterval(this.$_loadingTimeInterval);
      this.$_loadingTimeInterval = null;
    },
    startLoading() {
      this.loading = true;
      this.loadingTime = 0;
    },
  },
  created() {
    // this.getReadApi(76, 1);
  },

  mounted() {
    this.startLoading();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #864c00;
}
.readsubject {
  margin: 10px;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
}
.readbody {
  text-align: left;
  margin: 10px;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
  min-height: 400px;
}
</style>
