<template>
  <div class="read shadowsubject">
    <b-breadcrumb :items="bcitems"></b-breadcrumb>

    <div class="shadowsubject2">
      <b-tabs content-class="mt-3">
        <b-tab :title="readResponse.subject" active>
          <!-- <p>News - Page {{ currentPage }} - {{ $route.params.id }} - {{ $route.params.pageid }}</p> -->

          <div class="overflow-auto">
            <b-pagination
              v-model="currentPage"
              :total-rows="rows"
              :per-page="perPage"
              :currentPage="currentPage"
              :current-page="$route.params.pageid"
              aria-controls="my-table"
              @change="callReadApi"
            ></b-pagination>
          </div>
          <h1 class="readsubject">{{ readResponse.subject }}</h1>
          <br />

          <div class="readbody">
            <b-tabs content-class="mt-3">
              <b-tab :title="readResponse.subject + ''" active>
                <div v-html="'' + readResponse.body" class="readbody2"></div>

                <div  v-if="ythrefs.length>0">
                  <youtube
                    :video-id="videoId"
                    ref="youtube"
                    @playing="playing"
                  ></youtube>
                  <button @click="playVideo">play</button>
                  <ul>
                    <li
                      v-for="ythref in ythrefs"
                      :key="ythref"
                      v-bind:href="'/vue/ytplayer/' + ythref + ''"
                    >
                      <a v-bind:href="'/vue/ytplayer/' + ythref + ''">
                        {{ ythref }}
                      </a>
                    </li>
                  </ul>
                </div>
              </b-tab>

              <b-tab :title="'Videos'">
                <youtube
                  :video-id="videoId"
                  ref="youtube"
                  @playing="playing"
                ></youtube>
                <button @click="playVideo">play</button>
                <ul>
                  <li
                    v-for="ythref in ythrefs"
                    :key="ythref"
                    v-bind:href="'/vue/ytplayer/' + ythref + ''"
                  >
                    <a v-bind:href="'/vue/ytplayer/' + ythref + ''">
                      {{ ythref }}
                    </a>
                  </li>
                </ul>
              </b-tab>
              <b-tab :title="'more...'">
                <p v-html="'' + readResponse.body" class="readbody2"></p>
              </b-tab>
            </b-tabs>

            <!-- TEST -->

            <ul>
              <li
                v-for="ythref in ythrefs"
                :key="ythref"
                v-bind:href="'/vue/ytplayer/' + ythref + ''"
              >
                <a v-bind:href="'/vue/ytplayer/' + ythref + ''"
                  >{{ ythref }}
                </a>
              </li>
            </ul>
            <!-- eof TEST -->
          </div>

          <!-- <h4>{{ indexResponse }}</h4> -->
        </b-tab>

        <!-- <b-tab title="More" @click="startLoading()">
          <p>More</p>
          <b-row>
            <b-col md="auto">
              <b-calendar
                v-model="calvalue"
                @context="onContext"
                locale="en-US"
              ></b-calendar>
            </b-col>
            <b-col>
              <p>
                Value: <b>'{{ calvalue }}'</b>
              </p>
              <p class="mb-0">Context:</p>
              <pre class="small">{{ context }}</pre>
            </b-col>
          </b-row>
        </b-tab> -->
      </b-tabs>
    </div>

    <div class="shadowborde2r">
      <runtime-template-compiler
        :template="cfg.resourceConfiguration.novideofound"
      />
    </div>

    <!-- <div class="shadowborder">
      <center>
        <div class="overflow-auto">
          <b-pagination
            class="brownx2"
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            :currentPage="currentPage"
            :current-page="$route.params.pageid"
            aria-controls="my-table"
            @change="callReadApi"
          ></b-pagination>
        </div>
      </center>
    </div> -->
  </div>
</template>

<script>
import api from "../backend-api";
import Routes from "../config/Routes";
import Configuration from "../config/Configuration";

import CoreAPI from "../api/external/core/CoreAPI";

import Util from "../api/util.vue";

export default {
  name: "read",
  props: {
    hellomsg: { type: String, required: true },
  },

  data() {
    return {
      id: this.$route.params.id !== undefined ? this.$route.params.id : 1,
      pageid:
        this.$route.params.pageid !== undefined ? this.$route.params.pageid : 1,
      cfg: Configuration,
      util: Util,
      perPage: 1,
      currentPage: this.pageid !== undefined ? this.pageid : 1,
      msg: Routes.routes["read"].desc,
      readResponse: [],
      subject: "",
      body: "",
      errors: [],
      hrefs: [],
      ythrefs: [],
      videoId: "WmL1UDxfsGY",
      context: null,
      progressvalue: 45,
      calvalue: "",
      max: 100,
      loading: false,
      loadingTime: 0,
      maxLoadingTime: 3,
      novideos: "N/A",
      bcitems: [
        {
          text: "Home",
          href: Configuration.host + "/",
        },
        {
          text: "Index",
          href: Configuration.host + "/index",
        },
        {
          text: "List",
          href: Configuration.host + "/list/" + this.$route.params.id + "/1",
        },
        {
          text: "Read",
          href:
            Configuration.host +
            "/list/" +
            this.$route.params.id +
            "/" +
            this.pageId,
          active: true,
        },
      ],
      coreAPI: CoreAPI,
    };
  },
  computed: {
    player() {
      return this.$refs.youtube.player;
    },
    rows() {
      return this.readResponse.size;
    },
  },
  watch: {
    loading(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.clearLoadingTimeInterval();

        if (newValue) {
          this.$_loadingTimeInterval = setInterval(() => {
            this.loadingTime++;
          }, 1000);
        }
      }
    },
    loadingTime(newValue, oldValue) {
      if (newValue !== oldValue) {
        if (newValue === this.maxLoadingTime) {
          this.loading = false;
        }
      }
    },
  },

  methods: {
    onContext(ctx) {
      this.context = ctx;
    },
    callReadApi(value) {
      this.currentPage = value;
      this.getReadApi(this.id, value);
    },
    getReadApi(id, pageId) {
      this.currentPage = pageId;
      api
        .getRead(id, pageId)
        .then((response) => {
          this.readResponse = response.data;
          this.readResponse.body = this.getrenderBody(this.readResponse.body);
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
    getrenderBody(text) {
      var result;
      try {
        if (text !== undefined && text.length > 0) {
          var links = this.coreAPI.methods.getLinks(text);
          // console.log("1st JSON:" + JSON.stringify(links)+"Size:"+links.length);
          this.hrefs = this.coreAPI.methods.getLinksValues(links);
          // console.log("JSON:" + JSON.stringify(this.hrefs)+"Size:"+this.hrefs.length);
          this.ythrefs = this.coreAPI.methods.getYoutubeVideoLinks(this.hrefs);
          // console.log("JSON:" + JSON.stringify(this.hrefs)+"Size:"+this.hrefs.length);
          result = text;
          if (this.ythrefs.length > 0) {
            this.videoId = this.ythrefs[0];
            // result = text + "<hr>" + this.coreAPI.methods.getFinalBody(this.ythrefs);
            this.novideos = "<p>Video found</p>";
          } else {
            this.novideos = "<p>No video found</p>";
          }
          // this.coreAPI.methods.getYoutubeVideos(this.ythrefs);
        } else {
          result = "<h1>Not Found</h1>";
        }
      } catch (error) {
        console.error(error);
        console.error(error.stack);
      }
      return result;
    },
    playVideo() {
      this.player.playVideo();
    },
    playing() {
      console.log("o/ we are watching!!!");
    },
    setProgressValue() {
      this.progressvalue = Math.random() * this.max;
    },

    clearLoadingTimeInterval() {
      clearInterval(this.$_loadingTimeInterval);
      this.$_loadingTimeInterval = null;
    },
    startLoading() {
      this.loading = true;
      this.loadingTime = 0;
    },
  },
  created() {
    this.$_loadingTimeInterval = null;
    // console.log("Component has been created!");
    this.id =
      this.id !== undefined
        ? this.id
        : this.$route.params !== undefined
        ? this.$route.params.id
        : 1;
    this.pageId =
      this.pageId !== undefined
        ? this.pageId
        : this.$route.params !== undefined
        ? this.$route.params.pageid
        : 1;
    this.currentPage = this.pageId;
    // console.log("Forum:" + this.id + " Page" + this.currentPage);
    this.getReadApi(this.id, this.pageId);
  },

  mounted() {
    this.startLoading();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #864c00;
}
.readsubject {
  border: 2px solid rgb(178, 83, 0);
  border-radius: 15px;
  margin: 10px;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
}
.readbody {
  border: 2px solid rgb(178, 83, 0);
  border-radius: 15px;
  text-align: left;
  margin: 10px;
  padding: 2px;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
  min-height: 400px;
}
.novideos {
  position: default;
  margin-bottom: 0px;
  padding-bottom: 0px;
}
</style>
