<template>
  <div>
    <h1>
      <b-badge variant="success"
        >Welcome, {{ store.getters.getUserName }}</b-badge
      >
    </h1>
    <h5>
      You've successfully logged in and redirected to this protected area :)
    </h5>
    <!-- <div>Session:{{ session }}</div>
    <div>Session:{{ store.getters.getSession }}</div> -->
    <hr />

    <b-tabs content-class="mt-3">
      <b-tab title="Profile">
        <p>Welcome, {{ store.getters.getUserName }}</p>
        <p>Please dont forget to logout.</p>
        <p>Check authentication.</p><hr>
        <a href="/vue/import?requiresRedirect=/vue/import">Import</a><hr>
        <b-btn variant="primary" @click="getSecuredTextFromBackend()"
          >Call the secured API</b-btn
        >
      </b-tab>
      <b-tab title="Post/Edit" @click="startLoading()">
        <h1>Post</h1>
        <input
          type="button"
          id="addurl"
          value="Add Url"
          @click="addURL"
        /><input type="button" id="addimg" value="Add Img" /><input
          type="button"
          id="addyt"
          value="Add YTApi"
          @click="addURL"
        /><input type="button" id="adddl" value="Add DLApi" /><input
          type="button"
          id="eclear"
          value="Clear"
          @click="clear"
        />
        <!-- <form method="POST" id="insertForm" name="insertForm"> -->
        <form id="insertForm" name="insertForm">
          <!-- {{ session }} -->
          <input
            type="hidden"
            v-model="value2"
            id="value2"
            name="value2"
            value="1"
          />
          <fieldset>
            <legend>Forum</legend>

             <select v-model="value1" name="value1" id="value1">
              <option v-for="option in options" v-bind:key="option.value" v-bind:value="option.value">
                {{ option.text }}
              </option>
            </select>
            <span>Selected: {{ value1 }}</span>
            
          </fieldset>
          <fieldset>
            <legend>Zeitstempel</legend>
            <input
              v-model="cdate"
              name="cdate"
              id="cdate"
              type="date"
              value="2021-41-23 22:24:11"
              data-date-format="yy-mm-dd"
              data-role="date"
            />

          </fieldset>
          <fieldset>
            <legend>Subjekt</legend>
            <input
              v-model="subject"
              value="subject"
              type="text"
              id="subject"
              name="subject"
              placeholder="type here..."
              size="40;"
            /><br />
          </fieldset>
          <fieldset>
            <legend>Body</legend>
            <textarea
              v-model="body"
              name="ebody"
              id="ebody"
              rows="10"
              placeholder="type here..."
              style="min-width: 90%; width: 90%"
            ></textarea>
          </fieldset>
          <fieldset>
            <select name="isProtected" id="isProtected">
              <option value="0">no protection</option>
              <option value="1">protection</option>
              <option value="2">require admin rights</option>
              <option value="99">hidden</option></select
            ><select name="isPHPCode" id="isPHPCode">
              <option value="0">is no code</option>
              <option value="1">is code</option>
            </select>
          </fieldset>
          <fieldset>
            <label>IsUpdate</label
            ><input
              type="checkbox"
              id="isUpdate"
              name="isUpdate"
              checked=""
            /><br /><input
              type="button"
              id="btnSubmitModal"
              name="btnSubmit"
              value="submit"
              data-toggle="modal"
              data-target="#submitmodal"
              @click="getPost"
            /><br />
          </fieldset>
        </form>
      </b-tab>
      <b-tab title="Info" @click="startLoading()">
        <p>Welcome, {{ store.getters.getUserName }}</p>
        <p>Check authentication.</p>
        <b-btn variant="primary" @click="getSecuredTextFromBackend()"
          >Call the secured API</b-btn
        >
        <p>.</p>
      </b-tab>
      <b-tab title="More" @click="startLoading()">
        <p>Welcome, {{ store.getters.getUserName }}</p>
        <p>Check authentication.</p>
        <b-btn variant="primary" @click="getSecuredTextFromBackend()"
          >Call the secured API</b-btn
        >
      </b-tab>
    </b-tabs>

    <fieldset></fieldset>
    <div id="msg"></div>

    <hr />

    <div v-if="securedApiCallSuccess">
      <b-badge variant="success">API call</b-badge> Full response:
      {{ backendResponse }} <b-badge variant="success">successful</b-badge>
    </div>
    <div v-if="errors">
      <b-badge variant="warning">API call</b-badge> {{ errors }}
      <b-badge variant="warning">NOT successful</b-badge>
    </div>
  </div>
</template>

<script>
import api from "../../backend-api";
import store from "../../../store";
// import Cookie from "./cookie/Cookie";

export default {
  name: "protected",
  // value1: 1,
  // value2: 1,
  // subject: "",
  // body: "",
  created(){
    console.log("RUNNING PROTECTED PAGE");
    this.callgetFullIndex();
  },
  data() {
    return {      
      value1: 1,
      value2: 1,
      subject: "",
      body: "",
      cdate: new Date(),
      store: store,
      session: store.getters.getSession,
      userName: null,
      indexResponse: [],
      backendResponse: "",
      securedApiCallSuccess: false,
      errors: [],
      options: [
        { text: "All", value: "-1" }
        // { text: "Links", value: "87" },
      ],
    };
  },
  methods: {
    startLoading() {
      console.log("Test");
    },
    addURL() {
      this.body += "[url]x[/url]";
    },
    clear() {
      this.body = "";
    },
    callgetFullIndex() {
      api
        .getFullIndex()
        .then((response) => {
          this.indexResponse = response.data;
          console.log(JSON.stringify(this.indexResponse));
          for (var v in this.indexResponse["list"]){
            console.log/("V:"+JSON.stringify(this.indexResponse.list[v]));
            this.options.push({text:""+this.indexResponse.list[v].name, value:""+this.indexResponse.list[v].id});
          }
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
    getPost() {
      console.log("exec post");
      var value1 = this.value1;
      var value2 = this.value2;
      var cdate = this.cdate;
      var subject = this.subject;
      var body = this.body;
      var isProtected = this.isProtected;
      var isPHPCode = this.isPHPCode;
      alert(value1 + "#" + subject);
      console.log("Post");
      this.session = this.$store.getters.getSession; //Cookie.readCookie("PHPSESSID");
      console.log("Session:" + this.session);

      api
        // .getPost(store.getters.getUserName, store.getters.getUserPass)
        .getPost(
          value1,
          value2,
          cdate,
          subject,
          body,
          isProtected,
          isPHPCode,
          this.session
        )
        .then((response) => {
          console.log(
            "Response: '" +
              response.data +
              "' with Statuscode " +
              response.status
          );
          // this.session = response.data.session;
          // this.userName = store.getters.getUserName;
          this.securedApiCallSuccess = true;
          this.backendResponse = response.data;
          // console.log("OUT:" + JSON.stringify(response.data));
        })
        .catch((error) => {
          console.log("Error: " + error);
          this.errors = error;
        });
    },
    getSecuredTextFromBackend() {
      api
        .getSecured(store.getters.getUserName, store.getters.getUserPass)
        .then((response) => {
          console.log(
            "Response: '" +
              response.data +
              "' with Statuscode " +
              response.status
          );
          this.session = response.data.session;
          this.userName = store.getters.getUserName;
          this.securedApiCallSuccess = true;
          this.backendResponse = response.data;
          console.log("OUT:" + JSON.stringify(response.data));
        })
        .catch((error) => {
          console.log("Error: " + error);
          this.errors = error;
        });
    },
  },
};
</script>

