<template>
  <div class="search shadowsubject">
    <b-breadcrumb :items="items"></b-breadcrumb>   

    <h1>Search {{ hellomsg }}</h1>

    <h3>{{ cfg.subtitle }}</h3>

    <!-- <h1>Not working up to now. Please come back later...</h1> -->

    <p>Suche:{{ $route.params.query}}</p>

    <div class="d-flex align-items-center mb-3">
      <b-progress class="w-100" :max="maxLoadingTime" height="1.5rem">
        <b-progress-bar
          :value="
            ((loadingTime / maxLoadingTime) * 100).toFixed(2) * (1.5 / 10)
          "
          variant="danger"
        ></b-progress-bar>

        <b-progress-bar
          :value="
            ((loadingTime / maxLoadingTime) * 100).toFixed(2) * (2.5 / 10)
          "
          variant="warning"
        ></b-progress-bar>
        <b-progress-bar
          :value="((loadingTime / maxLoadingTime) * 100).toFixed(2) * (6 / 10)"
          variant="success"
        ></b-progress-bar>
      </b-progress>
      <b-progress-bar
        :value="loadingTime"
        :label="`${((loadingTime / maxLoadingTime) * 100).toFixed(2)}%`"
      ></b-progress-bar>

      <b-button class="ml-3" @click="startLoading()">Reload</b-button>
    </div>

    <div v-if="cfg.isApp">
      <b-badge variant="success">Application</b-badge>
      {{ cfg.isApp }} <b-badge variant="success">successful</b-badge>
    </div>
    <!-- <div v-if="errors">
      <b-badge variant="warning">API call</b-badge> {{ errors }} <b-badge variant="warning">NOT successful</b-badge>
    </div> -->

    <div class="shadowsubject2">
      <b-tabs content-class="mt-3">
        <b-tab v-if="cfg.isApp" title="App" @click="startLoading()">
          <p>.</p>
        </b-tab>        

        <b-tab
          id="my-tabs"
          :per-page="perPage"
          v-for="item in indexResponse.list"
          :title="item.name"
          :key="item.id"
          @click="callSearchApi(item.id)"
          >{{ item.name }}
          <div class="overflow-auto">
            <b-pagination
              v-model="currentPage"
              :total-rows="rows"
              :per-page="perPage"
              aria-controls="list-table"
              @change="callSearchApi"
            ></b-pagination>

            <b-list-group>
              <b-list-group-item
                id="list-table"
                :per-page="perPage"
                :current-page="currentPage"
                v-for="item in searchResponse"
                :key="item.id"
                v-bind:href="cfg.host + '/read/' + item.page + '/' + item.id"
                class="article"
              >
                {{ item.subject }}
              </b-list-group-item>
            </b-list-group>
          </div>
        </b-tab>
        
      </b-tabs>
    </div>

    <hr />
  </div>
</template>

<script>
import Vue from "vue";
import api from "../backend-api";
import Routes from "../config/Routes";
import Configuration from "../config/Configuration";
export default {
  name: "search",
  props: {
    hellomsg: { type: String, required: true },
  },
  computed: {
    rows() {
      return this.indexResponse.size !== undefined
        ? this.indexResponse.size
        : 0;
    },
  },
  data() {
    return {
      vue: Vue,
      query: this.$route.params.query,
      perPage: 3,
      currentPage: 1,
      cfg: Configuration,
      source: Configuration.bottomtitle,
      msg: Routes.routes["home"].desc,
      indexResponse: [],
      searchResponse: [],
      errors: [],
      context: null,
      progressvalue: 45,
      calvalue: "",
      max: 100,
      loading: false,
      loadingTime: 0,
      maxLoadingTime: 3,
      message: "Hello from the template compiler",
      template:
        `
          <h1>{{ message }}</h1>
        ` + Configuration.bottomtitle,

      items: [
        {
          text: "Home",
          href: "/",
        },
        {
          text: "Index",
          href: "/index",
          active: true,
        },
      ],
    };
  },
  watch: {
    loading(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.clearLoadingTimeInterval();

        if (newValue) {
          this.$_loadingTimeInterval = setInterval(() => {
            this.loadingTime++;
          }, 1000);
        }
      }
    },
    loadingTime(newValue, oldValue) {
      if (newValue !== oldValue) {
        if (newValue === this.maxLoadingTime) {
          this.loading = false;
        }
      }
    },
  },

  methods: {
    // computed: {
    //   dynamicComponent() {
    //     return {
    //       template: this.source,
    //     };
    //   },
    // },
    onContext(ctx) {
      this.context = ctx;
    },
    htmlCompile(s) {
      // return Vue.compile(s);
      return s;
    },
    // Fetches posts when the component is created.
    callIndexApi(value) {
      this.currentPage = value;
      this.currentPage = this.currentPage !== undefined ? this.currentPage : 1;
      api
        .getFullIndex(this.currentPage)
        .then((response) => {
          this.indexResponse = response.data;
          this.indexList = this.callRenderIndex(this.indexResponse);
          console.log(response.data);
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
    callSearchApi(value) {
      this.currentPage = value !== undefined ? value : 1;
      this.id = this.$route.params !== undefined ? this.$route.params.id : 1;
      this.id = this.id !== undefined ? this.id : 1;
      this.getSearchApi(value, this.currentPage);
    },
    getSearchApi(id, pageId) {
      id = id !== undefined ? id : 1;
      pageId = pageId !== undefined ? pageId : 1;
      console.log("ID:" + id + " Page:" + pageId);
      api
        .getSearch(id, pageId, this.query )
        .then((response) => {
          this.searchResponse = response.data;
          console.log(response.data);
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
    setProgressValue() {
      this.progressvalue = Math.random() * this.max;
    },
    callRenderIndex(data) {
      var result = "";
      result += "<ul>";
      for (v in data) {
        result += "<li>" + data[v] + "</li>";
      }
      result += "</ul>";
      return result;
    },
    clearLoadingTimeInterval() {
      clearInterval(this.$_loadingTimeInterval);
      this.$_loadingTimeInterval = null;
    },
    startLoading() {
      this.loading = true;
      this.loadingTime = 0;
    },
  },
  created() {
    this.$_loadingTimeInterval = null;
    console.log("Component has been created!");
    this.callIndexApi();
    //this.callSearchApi(1,1);
  },

  mounted() {
    this.startLoading();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #864c00;
}
.article {
  text-align: left;
}
.shadowsubject {
  margin: 10px;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
}
</style>
